import { EspecificacaoDTO } from './EspecificacaoDTO';

/**
 * Repositrio de Especificaes que armazena e carrega especificaes como objetos literais
 * Esses objetos servem para construir objetos de domnio especfico como do Editor e de Respostas
 */
export interface IEspecificacaoRepository {
    armazenar(especificacao: EspecificacaoDTO): void;
    carregar(id: string): EspecificacaoDTO;
}

/**
 * Erro gerado ao no encontrar uma especificao de um ID solicitado
 */
export class ErroEspecificacaoNaoEncontrada extends Error {
    constructor(id: string) {
        super(`Especificao com o id ${id} no localizada no repositrio`);
    }
}
